import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/home/Home'
import Table from '@/pages/table/Table'
import Form from '@/pages/form/Form'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/table',
      name: 'Table',
      component: Table
    },
    {
    	path: '/form',
    	name: 'Form',
    	component: Form
    }
  ]
})



